.form{
  max-height: 180px;
  padding: 10px;
  display: flex;
  gap: 20px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 6px;
  backdrop-filter: blur(15px);
  border-top: 1px solid rgba(255, 255, 255, 0.2);
  border-left: 1px solid rgba(255, 255, 255, 0.2);
  box-shadow: 5px 5px 30px rgb(0 0 0 / 20%);
}

.label{
  display: flex;
  gap: 10px;
  align-items: center;
}

.paragraph{
  width: 70px;
  color: #ebe5e5;
  font-family: "Roboto",sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.14;
  letter-spacing: .02em;
  @media screen and (min-width: 480px){
      width: 90px;
      font-size: 20px;
  }
  @media screen and (min-width: 1240px){
      width: 110px;
      font-size: 22px;
  }    
}

.input{
  padding: 3px;
  background-color: transparent;
  outline: none;
  border: none;
  border-bottom: 1px solid #ebe5e5;
  border-radius: 3px;
}

.button{
  display: block;
  padding: 5px;
  color: #ebe5e5;
  font-family: "Roboto",sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.14;
  letter-spacing: .02em;
  border: 2px solid #ebe5e5;
  border-radius: 2px;
  background-color: transparent;
  transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
  &:hover {
      color: #ffffff;
      border-color: #ffffff;
      transform: scale(1.1);
  }
  @media screen and (min-width: 480px){
      font-size: 20px;
  }
  @media screen and (min-width: 1240px){
      font-size: 24px;
  }
}